import { Component, HostListener } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent {

  /**
   * @summary current index of the user in the konami code sequence
   */
  private konamiIndex = 0;

  /**
   * @summary konami code keys
   * @see https://en.wikipedia.org/wiki/Konami_Code
   */
  private readonly konamiKeys = [
    'ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown',
    'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight',
    'KeyB', 'KeyQ'
  ];

  @HostListener('document:keyup', ['$event'])
  public keyPressed(event: KeyboardEvent): void {
    const pressed = event.code;

    if (pressed !== this.konamiKeys[this.konamiIndex]) {
      this.konamiIndex = 0;
      return;
    }

    if (++this.konamiIndex === this.konamiKeys.length) {
      this.konamiIndex = 0;

      window.location.href = 'https://bit.ly/IqT6zt';
    }
  }

}
