import { Component } from '@angular/core';

@Component({
  selector: 'app-content-intro',
  templateUrl: './content-intro.component.html',
  styleUrls: ['./content-intro.component.css']
})
export class ContentIntroComponent {

  /**
   * @summary main tags to describe myself
   */
  private descriptionTags: Array<string> = [
    'student',
    'software engineer',
    'coffee lover',
  ];

  /**
   * @summary separator between displayed tags
   */
  private tagSeparator = ' | ';

  public getFormattedTags(): string {
    return this.descriptionTags.join(this.tagSeparator);
  }

}
